import React, { Component } from "react";
import GoldMain from "../Home/images/Digital Gold/1.jpg";
import GoldF1 from "../Home/images/Digital Gold/Investment Amounts.svg";
import GoldF2 from "../Home/images/Digital Gold/Loans.svg";
import GoldF3 from "../Home/images/Digital Gold/Purity.svg";
import { withTranslation } from "react-i18next";
import reactHtmlParser from 'react-html-parser';

export class DigitalGold extends Component {
  render() {
    const{t} =this.props;
    return (
      <main>
        <div className="container container-wrapper">
          <div className="col-lg-7 col-md-6 mb-4">
            <h2>{t("DigitalGold:digitalGoldF1")}</h2>
            <p>{t("DigitalGold:digitalGoldF2")}</p>
            <a
              href="https://app.nivesh.com/home/index"
              target="_blank"
              className="btn btn-primary"
            >
              {t('CTAFooter:InvestNow1')}
            </a>
          </div>
          <div className="col-lg-5 col-md-6">
            <img src={GoldMain} alt="" className="img-fluid" />
          </div>
        </div>
        <div className="bg-lightblue ourPartCaptital">
          <div className=" container container-wrapper">
            <h2 className="text-center">{t("DigitalGold:digitalGoldF3")}</h2>
            <div className="row">
              <div className="card-deck">
                <div className="card">
                  <div className="card-body">
                    <img src={GoldF1} alt="" />
                    <h3>{t("DigitalGold:digitalGoldF4")}</h3>
                    <p className="small-text">
                    {t("DigitalGold:digitalGoldF5")}
                    </p>
                  </div>
                </div>
                <div className="card">
                  <div className="card-body">
                    <img src={GoldF2} alt="" />
                    <h3>{t("DigitalGold:digitalGoldF6")}</h3>
                    <p className="text-center small-text">
                    {t("DigitalGold:digitalGoldF7")}
                    </p>
                  </div>
                </div>
                <div className="card">
                  <div className="card-body">
                    <img src={GoldF3} alt="" />
                    <h3>{t("DigitalGold:digitalGoldF8")}</h3>
                    <p className="small-text">
                    {t("DigitalGold:digitalGoldF9")}
                    </p>
                  </div>
                </div>
              </div>
            </div>
            <div className="text-center mt-4">
              <a
                href="https://app.nivesh.com/home/index"
                target="_blank"
                className="btn btn-primary"
              >
                {t('CTAFooter:InvestNow1')}
              </a>
            </div>
          </div>
        </div>

        <div className="getStarted">
          <div className="container container-wrapper">
            <h2>{t("DigitalGold:digitalGoldF10")}</h2>
            <p>
            {t("DigitalGold:digitalGoldF11")}
            </p>
          </div>
        </div>

        <div className="getCustomer">
          <div className="container container-wrapper nps-productsection">
            <div className="row row justify-content-md-center">
              <div className="col-lg-12">
                <h2 className="text-white">{t("DigitalGold:digitalGoldF12")}</h2>
                <p className="text-white">
                {reactHtmlParser(t("DigitalGold:digitalGoldF13"))}
                </p>
                <ol className="text-white">
                  <li>{t("DigitalGold:digitalGoldF15")}</li>
                  <li>
                    {t("DigitalGold:digitalGoldF16")}
                  </li>
                  <li>
                  {t("DigitalGold:digitalGoldF17")}</li>
                  <li>
                  {t("DigitalGold:digitalGoldF18")}
                  </li>
                </ol>
              </div>
            </div>
          </div>
        </div>
        <div className="container container-wrapper">
          <h2>{t("DigitalGold:digitalGoldF19")}</h2>
          <p>{t("DigitalGold:digitalGoldF20")} </p>
          <ul>
            <li>{t("DigitalGold:digitalGoldF21")}</li>
            <li>
            {t("DigitalGold:digitalGoldF22")}
            </li>
            <li>
            {t("DigitalGold:digitalGoldF23")}
            </li>
          </ul>
        </div>

        <div className="getCustomer">
          <div className="container container-wrapper">
            <h2 className="text-center text-white">{t("DigitalGold:digitalGoldF24")}</h2>
            <div className="row row justify-content-center">
              <div className="col-lg-5 mb-4">
                <div className="card h-100">
                  <div className="card-body">
                    <h3>{t("DigitalGold:digitalGoldF25")}</h3>
                    <p className="text-center">
                    {t("DigitalGold:digitalGoldF26")}
                    </p>
                  </div>
                </div>
              </div>
              <div className="col-lg-5 mb-4">
                <div className="card h-100">
                  <div className="card-body">
                    <h3>{t("DigitalGold:digitalGoldF27")}</h3>
                    <p className="text-center">
                    {t("DigitalGold:digitalGoldF28")}
                    </p>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>

        <div className="container container-wrapper">
          <div className="row mt-4">
            <div className="col-lg-12">
              <h2 className="mb-4">{t("DigitalGold:digitalGoldF29")}</h2>
            </div>
            <div className="col-lg-12">
              <h3>{t("DigitalGold:digitalGoldF30")}</h3>
              <p>
              {t("DigitalGold:digitalGoldF31")}
              </p>

              <h3>{t("DigitalGold:digitalGoldF32")}</h3>
              <p>
              {t("DigitalGold:digitalGoldF33")}
              </p>

              <h3>{t("DigitalGold:digitalGoldF34")}</h3>
              <p>
              {t("DigitalGold:digitalGoldF35")}
              </p>
            </div>
          </div>
        </div>
      </main>
    );
  }
}

export default withTranslation() (DigitalGold)
